.App {
font-family: 'Roboto';
}
.MultiSelectDrop {
    width: 720px;
    position: absolute;
    top: 70px;
    left: 0px;
    z-index: 2;
}

.bntSelect {
    width: 100%;
    padding: 15px;
    background-color: white;
    cursor: pointer;
    border-bottom: none;
    border-top: none;
    border-left: none;
    border-right: 1px solid #E5E5E5;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border: 1px solid #E5E5E5;
    display: flex;
    
}

.bntSelect span {
    color: #A8A8A8;
    margin-top: 10px;
    display: flex;
    
}

.bntSelect span > p {
   
    color: #000000;
   margin-left: 0px;
   border: 1px solid #A8A8A8;
   border-radius: 8px;
   padding: 8px;
   margin-right: 8px;
}


.bntSelect img {
    margin-left: 10px;
}

.Planos {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    border: 1px solid #A8A8A8;
    align-items: flex-start;
    display: none;
    border-radius: 8px;

    min-height: 100px;
    z-index: 100;
}

.Planos p{
    padding: 30px 20px;
    width: 679px;
    cursor: pointer;
    font-weight: 400;
    border-radius: 8px;
    color: #000000
   
}

.Planos span:hover {
    background-color: #d3cece;
}

.MultiSelectDrop:hover .Planos {
    display: flex;
}

.PlanosWrapper {
    padding: 20px
}

input[type="checkbox"] {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    margin-right: 10px;
}

label {
    margin-right: 10px;
    
}


.DropWrapper {
    border: 1px solid #E5E5E5;
    border-radius: 8px;
    min-height: 100px;
    height: auto;
    position: relative;
}

.DropWrapper .DropWrapperTop {
    display: flex;
}

.btndrop {
    width: 240px;
    padding: 15px;
    background-color: white;
    cursor: pointer;
    border-bottom: none;
    border-top: none;
    border-left: none;
    border-right: 1px solid #E5E5E5;
    display: flex;
    flex-direction: column;
    align-items: flex-start;    
    
}

/* .btndrop:hover {
    border: 1px solid black;
} */
.btndrop p {
    color: #A8A8A8;
    margin-top: 10px;
    
}

.btndrop img {
    margin-left: 10px;
}



.dropContent {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    border: 1px solid #A8A8A8;
    align-items: flex-start;
    display: none;
    border-radius: 8px;
    z-index: 4;
    
}

.dropContent span {
    padding: 10px 20px;
    width: 190px;
    cursor: pointer;
    font-weight: 200;
   
}


.dropContent span:hover {
    background-color: #d3cece;
}


.dropdown:hover .dropContent{
    display: flex;
    position: absolute;
 }
